import React from 'react';
import classNames from 'classnames';

import Aside from 'components/Aside';
import Modal from "components/Modal";

import axios from 'axios';

class Home extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			modalOpen: false,
			userInfo: {
				birth: '',
				gender: 'Man',
				name: '',
				password: 'test1234',
				phone: '',
				role: 'ROLE_ADMIN',
				// address: '',
				// ticket: '',
			},
			submitDisabled: true,
		}
	}

	componentDidMount() {
	}

	openModal = () => {
		this.setState({
			modalOpen: true
		});
	};

	closeModal = () => {
		this.setState({
			modalOpen: false
		});
	};

	onInputChange = (e) => {
		var target = e.target;

		this.setState({
			...this.state,
			userInfo: {
				...this.state.userInfo,
				[target.name]: target.value
			}
		});
	}

	onSubmit = () => {
		try{
			let userInfo = JSON.parse(JSON.stringify(this.state.userInfo));

			axios.post("http://146.56.45.3:8080/api/auth/user/signup" , JSON.stringify(userInfo), {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					'Cache-Control': 'no-cache',
					'Accept': 'application/json'
				}})
				.then(res =>{
					// console.log("res.data.accessToken : " + res.data);
					// axios.defaults.headers.common['Authorization'] = 'Bearer ' + res.data;
					const resData = JSON.parse(JSON.stringify(res.data));
					console.log(resData);
					console.log('ins_dtm :' + resData.data.ins_dtm);
					console.log('upd_dtm :' + resData.data.upd_dtm);
					this.openModal();
				})
				.catch(ex=>{
					console.log("login requset fail : " + ex);
				})
				.finally(()=>{console.log("login request end")});
			}catch(e){
				console.log(e);
			}
	}

	validate = () => {
		var objArr = Object.values(this.state.userInfo);
		if(objArr.indexOf('') === -1) {
			this.setState({
				...this.state,
				submitDisabled: false,
			});
		}
	}

	render() {
		const {modalOpen, userInfo, submitDisabled} = this.state;

		return (
			<div id={'wrap'} className={classNames('register_wrap')}>
				<Aside link={'register'}/>
				<div className="container">
					<div className={'notify_area'}>
						<h2> </h2>
					</div>

					<form className="form_area" autoComplete={'on'}>
						<div className={'form_box'}>
							<input type="text" className={'form_input'} placeholder={'  '} required={true} onChange={(e) =>this.onInputChange(e)} onKeyUp={this.validate}  name={'name'}/>
							<label className={'form_label'}></label>
						</div>
						<div className={'form_box'}>
							<input type="text" className={'form_input'} placeholder={'  '} required={true} onChange={(e) =>this.onInputChange(e)} onKeyUp={this.validate} name={'birth'}/>
							<label className={'form_label'}></label>
							<p className={'form_detail'}>) 1992.02.28</p>
						</div>
						<div className={'form_box'}>
							<input type="text" className={'form_input'} placeholder={'01012345678'} required={true} onChange={(e) =>this.onInputChange(e)} onKeyUp={this.validate} name={'phone'}/>
							<label className={'form_label'}></label>
							<p className={'form_detail'}>-    </p>
						</div>
						<div className={'form_box'}>
							<input type="text" className={'form_input'} placeholder={'  '} required={true} onChange={(e) =>this.onInputChange(e)} onKeyUp={this.validate} name={'password'}/>
							<label className={'form_label'}></label>
						</div>
						<div className={'form_box'}>
							<input type="text" className={'form_input'} placeholder={'0'} required={true} onKeyUp={this.validate} name={'ticket'}/>
							<label className={'form_label'}> ()</label>
							<p className={'form_detail'}> </p>
						</div>
					</form>

					<div className={'register_area'}>
						<button type={'submit'} className={'btn_register'} disabled={submitDisabled} onClick={this.onSubmit} > </button>
					</div>
				</div>

				<Modal open={modalOpen} close={this.closeModal} header=" ">
					<div className={'title'}><strong></strong>  ID PW .</div>
					<p className={'description'}>
						         . <br/>
						       4 .
					</p>
					<ul className={'user_information'}>
						<li className={'row'}>
							<span className={'cell'}>ID</span>
							<span className={'cell'}>{userInfo.name}</span>
						</li>
						<li className={'row'}>
							<span className={'cell'}>PW</span>
							<span className={'cell'}>1234</span>
						</li>
					</ul>
				</Modal>
			</div>
		);
	}
}

export default Home;

